import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg from './assets/page13.png';
import hint from './assets/hint6.png';

function Page13({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage1');
    const { answer } = game.state.stage1.answers.find((answer) => answer.pathname === '/page5-1') as Answer || {}

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="page13" />
            <Name>
                {answer || ''}
            </Name>
            <Answer 
                answers={["오른쪽"]} 
                onSuccess={(answer) => { 
                    game.goto('/page14')
                    game.answer(answer)
                 }} 
                onFailure={() => { game.goto('/wrong6'); }} 
                onHint={onShowHint} 
            />
        </Wrapper>
    )
}

export default { 
    pathname: '/page13', 
    component: withHintModal(Page13)(hint) 
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`
const Name = styled.span`
    position: absolute;
    top: 32.61%;
    left: 28%;
    color: #362b28;
    font-size: 140%;
    font-weight: bold;
    background-color: transparent;
    font-family: aggro;
`