import React, { useRef, useState } from 'react'
import styled from 'styled-components';
import BackgroundImage from '../../components/BackgroundImage';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame'
import backgroundImg from './assets/home-code-bg.png';
// import lockIconImg from './assets/code-lock-icon.png';
import sendIconImg from './assets/code-send-icon.png';
import gameButtonBgImg from './assets/main-btn-background.png';
import gameButtonBgImgHover from './assets/main-btn-background-hover.png';
// import gameButtonBgLockImg from './assets/home-stage-btn-background-lock.png';
import stage1IconImg from './assets/home-stage1-icon.png';
// import stageTitle1IconImg from './assets/home-stage-1-red.png';
// import stageLockIcon from './assets/home-stage-lock-icon.png';
import codeWrongImg from './assets/code-wrong-text.png';

const stages = [
    {   
        name: 'stage1',
        icon: stage1IconImg,
        // titleIcon: stageTitle1IconImg,
        stageName: '게임 시작',
        path: '/description'
    }
] as const;

function Intro1() {
    const [isCorrectCode, setIsCorrectCode] = useState(true);
    const { state, successEnterCode, goto } = useGame();
    const codeRef = useRef<HTMLInputElement>(null);

    const handleEnterCode = () => {
        const { validity } = codeRef.current as HTMLInputElement;

        if (validity.valid) {
            successEnterCode();
            setIsCorrectCode(true);
        } else {
            setIsCorrectCode(false);
        }
    }

    return (
        <Layout>
            <BackgroundImage src={backgroundImg} alt="intro" />
            {
                state.isSuccessEnterCode ? 
                        <GameWrapper>
                            {
                                stages.map((stage, idx) => {
                                    if (state[stage.name].open) {
                                        return (
                                            <GameButtonWrapper key={stage.stageName}>
                                                <GameButton onClick={() => { goto(stage.path)}}>
                                                    <GameIcon src={stage.icon} alt={stage.stageName}/>
                                                    <GameName>
                                                        {stage.stageName}
                                                    </GameName>
                                                </GameButton>
                                            </GameButtonWrapper>
                                        )
                                    } else {
                                        return (
                                            <GameButtonWrapper key={stage.stageName}>
                                            </GameButtonWrapper>
                                        );
                                    }
                                })
                            }
                        </GameWrapper> 
                    :   
                        <CodeWrapper>
                            <ContentDesc>
                                입장 코드를 입력하세요.(입장코드 : 0000)
                            </ContentDesc>
                            <CodeContentWrapper>
                                <CodeInput 
                                    ref={codeRef}
                                    required 
                                    onKeyDown={({ code }) => {
                                        if (code === 'Enter') {
                                            handleEnterCode();
                                        }
                                    }} 
                                    type="text" 
                                    pattern="0000" 
                                    maxLength={4}
                                />
                                <CodeIconButton type="button" onClick={handleEnterCode}>
                                    <CodeIconImage src={sendIconImg} alt="send icon" />
                                </CodeIconButton>
                            </CodeContentWrapper>
                            {!isCorrectCode && (
                                    <CodeWrongAnswerImgWrapper>
                                        <img src={codeWrongImg} alt=""/>
                                    </CodeWrongAnswerImgWrapper>
                                )
                            }
                        </CodeWrapper>
            }
        </Layout>
    )
}

export default { pathname: '/', component: Intro1 } as RouteMap;


// code

const CodeWrapper = styled.div`
    position: absolute;
    top: 61%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 74%;
`

const ContentDesc = styled.p`
    color: #fff;
    text-align: center;
    margin-bottom: 33px;
`

const CodeContentWrapper = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    border: 1px solid #fff;
    border-radius: 8vw;
    padding: 1.5vw;
`

const CodeIconImage = styled.img`
    width: 16%;
    height: auto;
`

const CodeIconButton = styled.button`
        width: 16%;
        height: auto;
        
        img {
            display: block;
            width: 100%;
            height: auto;
        }
`

const CodeInput = styled.input`
    background-color: #fff;
    flex: 1;
    margin: 0 5px;
    text-align: center;
    width: 100%;
    border-radius: 30px;
`

const CodeWrongAnswerImgWrapper = styled.div`
    width: 87%;
    background-color: rgba(255, 255, 255, 0.6);
    margin: 0 auto;
    margin-top: 1.5vw;
    border-radius: 3vw;
    padding: 1vw;

    img {
        display: block;
        width: 95%;
        height: auto;
        margin: 0 auto;
    }
`

// game

const GameWrapper = styled.ul`
    position: absolute;
    top: 65%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 70%;

    li + li {
        margin-top: 10%;
    }

`

const GameButtonWrapper = styled.li`
    position: relative;
    border-radius: 50px;
    padding: 7px;
    border: 1px solid #fff;
`

const GameButton = styled.button`
    display: flex;
    align-items: center;
    padding: 5px;
    border-radius: 50px;
    background-image: url(${gameButtonBgImg});
    background-size: cover;
    text-decoration: none;
    color: #000;
    width: 100%;
    &:hover {
        background-image: url(${gameButtonBgImgHover});
        color: #fff;
      }
`


const GameIcon = styled.img`
    width: 21%;
    height: auto;
    margin: 5px;
`

const GameName = styled.p`
    text-align: center;
    flex: 1;
    font-size: 130%;
    font-weight: bold;
    margin-top: 1%;
    margin-right: 15%;
    font-family: aggro;
`

