
import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg from './assets/page12.png';
import gotoBtnImg1 from './assets/page12-btn.png';
import gotoBtnImg1_hover from './assets/page12-btn-hover.png';


function Page12() {
    const game = useGame('stage1');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="page12" />
            <Button onClick={() => { game.goto('/page13') }}>
                <img src={gotoBtnImg1} alt="go to page13" />
                <img className="hover" src={gotoBtnImg1_hover} alt="go to page13" />
            </Button>
        </Wrapper>
    )
}



export default { 
    pathname: '/page12', 
    component: Page12
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    min-height: 100vh;
    padding-bottom: 20vw;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const Button = styled.button`
    display: block;
    margin: 0 auto;

    img {
        display: block;
        width: 40vw;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`