import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg from './assets/page7.png';
import gotoBtnImg1 from './assets/page7-btn1.png';
import gotoBtnImg1_hover from './assets/page7-btn1-hover.png';
import gotoBtnImg2 from './assets/page7-btn2.png';
import gotoBtnImg2_hover from './assets/page7-btn2-hover.png';

function Page7() {
    const game = useGame('stage1');
    const { answer } = game.state.stage1.answers.find((answer) => answer.pathname === '/page5-1') as Answer || {}

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="page7" />
            <Name>
                {answer || ''}
            </Name>
            <ButtonWrapper>
                <Button onClick={() => { game.goto('/page7-1') }}>
                    <img src={gotoBtnImg1} alt="go to page7-1" />
                    <img className="hover" src={gotoBtnImg1_hover} alt="go to page7-1" />
                </Button>
                <Button onClick={() => { game.goto('/page8') }}>
                    <img src={gotoBtnImg2} alt="go to page8" />
                    <img className="hover" src={gotoBtnImg2_hover} alt="go to page8" />
                </Button>
            </ButtonWrapper>
        </Wrapper>
    )
}

export default { 
    pathname: '/page7', 
    component: Page7,
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`


const Name = styled.span`
    position: absolute;
    top: 71%;
    left: 53.5%;
    color: #362b28;
    font-size: 145%;
    font-weight: light;
    background-color: transparent;
    font-family: baskin;
`


const ButtonWrapper = styled.div`
    width: 70vw;
    margin: 0 auto;

    button + button {
        margin-top: 5vw;
    }
`

const Button = styled.button`
    display: block;
    margin: 0 auto;

    &::after {
        opacity: 0;
    }
  
    img {
        display: block;
        width: 100%;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`