import React from 'react'
import styled from 'styled-components';
// import Answer from '../../components/Answer';/
import Layout from '../../components/Layout';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg from './assets/page6.png';
import gotoBtnImg1 from './assets/page6-btn1.png';
import gotoBtnImg1_hover from './assets/page6-btn1-hover.png';
import gotoBtnImg2 from './assets/page6-btn2.png';
import gotoBtnImg2_hover from './assets/page6-btn2-hover.png';
import gotoBtnImg3 from './assets/page6-btn3.png';
import gotoBtnImg3_hover from './assets/page6-btn3-hover.png';
import gotoBtnImg4 from './assets/page6-btn4.png';
import gotoBtnImg4_hover from './assets/page6-btn4-hover.png';
import gotoBtnImg5 from './assets/page6-btn5.png';
import gotoBtnImg5_hover from './assets/page6-btn5-hover.png';
import hint from './assets/hint6.png';

function Page6({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage1');
    const { answer } = game.state.stage1.answers.find((answer) => answer.pathname === '/page5-1') as Answer || {}

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="stage1-5" />
            <Name>
                {answer || ''}
            </Name>
            <ButtonWrapper>
                <Button onClick={() => { game.goto('/wrong2') }}>
                    <img src={gotoBtnImg1} alt="go to wrong2" />
                    <img className="hover" src={gotoBtnImg1_hover} alt="go to wrong2" />
                </Button>
                <Button onClick={() => { game.goto('/wrong2') }}>
                    <img src={gotoBtnImg2} alt="go to wrong2" />
                    <img className="hover" src={gotoBtnImg2_hover} alt="go to wrong2" />
                </Button>
                <Button onClick={() => { game.goto('/wrong3') }}>
                    <img src={gotoBtnImg3} alt="go to wrong3" />
                    <img className="hover" src={gotoBtnImg3_hover} alt="go to wrong3" />
                </Button>
                <Button onClick={() => { game.goto('/wrong4') }}>
                    <img src={gotoBtnImg4} alt="go to wrong4" />
                    <img className="hover" src={gotoBtnImg4_hover} alt="go to wrong4" />
                </Button>
                <Button onClick={() => { game.goto('/page7') }}>
                    <img src={gotoBtnImg5} alt="go to page7" />
                    <img className="hover" src={gotoBtnImg5_hover} alt="go to page7" />
                </Button>
            </ButtonWrapper>
        </Wrapper>
    )
}

export default { 
    pathname: '/page6', 
    component: withHintModal(Page6)(hint) 
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const ButtonWrapper = styled.div`
    width: 50vw;
    margin: 0 auto;

    button + button {
        margin-top: 5vw;
    }
`

const Button = styled.button`
    display: block;
    margin: 0 auto;

    &::after {
        opacity: 0;
    }
  
    img {
        display: block;
        width: 100%;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`

const Name = styled.span`
    position: absolute;
    top: 42.55%;
    left: 74%;
    color: #362b28;
    font-size: 130%;
    font-weight: bolder;
    background-color: transparent;
    font-family: nanum;
`