import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg from './assets/page18.png';
import gotoBtnImg1 from './assets/page18-btn.png';
import gotoBtnImg1_hover from './assets/page18-btn-hover.png';

function Page18() {
    const game = useGame('stage1');
    const { answer } = game.state.stage1.answers.find((answer) => answer.pathname === '/page5-1') as Answer || {}

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="page8" />
            <Name>
                {answer || ''}
            </Name>
            <Button onClick={() => { game.goto('/') }}>
                <img src={gotoBtnImg1} alt="go to page9" />
                <img className="hover" src={gotoBtnImg1_hover} alt="go to page9" />
            </Button>

        </Wrapper>
    )
}

export default { 
    pathname: '/page18', 
    component: Page18,
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`


const Name = styled.span`
    position: absolute;
    top: 50.08%;
    left: 43%;
    color: #181818;
    font-size: 143%;
    background-color: transparent;
    font-family: gungsuh;
    font-weight: bolder;
`


const Button = styled.button`
    display: block;
    margin: 0 auto;

    img {
        display: block;
        width: 40vw;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`