import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg from './assets/page15.png';
import gotoBtnImg1 from './assets/page15-btn1.png';
import gotoBtnImg1_hover from './assets/page15-btn1-hover.png';
import gotoBtnImg2 from './assets/page15-btn2.png';
import gotoBtnImg2_hover from './assets/page15-btn2-hover.png';

function Page15() {
    const game = useGame('stage1');
    return (
        <Wrapper>
            <StageImage src={stageImg} alt="page15" />
            <ButtonWrapper>
                <Button onClick={() => { game.goto('/page15_1') }}>
                    <img src={gotoBtnImg1} alt="go to page15-1" />
                    <img className="hover" src={gotoBtnImg1_hover} alt="go to page15-1" />
                </Button>
                <Button onClick={() => { game.goto('/page16') }}>
                    <img src={gotoBtnImg2} alt="go to page16" />
                    <img className="hover" src={gotoBtnImg2_hover} alt="go to page16" />
                </Button>
            </ButtonWrapper>
        </Wrapper>
    )
}

export default { 
    pathname: '/page15', 
    component: Page15,
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`


const ButtonWrapper = styled.div`
    width: 70vw;
    margin: 0 auto;

    button + button {
        margin-top: 5vw;
    }
`

const Button = styled.button`
    display: block;
    margin: 0 auto;

    &::after {
        opacity: 0;
    }
  
    img {
        display: block;
        width: 100%;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`