import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import PressButton from '../../components/PressButton';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg1 from './assets/page11.png';
import stageImg2 from './assets/page11-1.png';
import hint from './assets/hint5.png';
import gotoBtnImg from './assets/page11-btn.png';
import gotoBtnImg_hover from './assets/page11-btn-hover.png';
import { useRef } from 'react';

let hasVisited11_1 = false;

function Page11({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage1');
    const contentRef = useRef<HTMLImageElement>(null);

    return (
        <Wrapper>
            <StageImage src={stageImg1} alt="page11" />
            <StyledPressButton 
                timing={0}
                showContent={hasVisited11_1}
                showButton={true}
                onEnd={() => {
                    window.setTimeout(() => {
                        const contentEl = contentRef.current as HTMLImageElement;
                        
                        window.scrollTo({ top: contentEl.offsetTop, left:0, behavior: 'smooth' })
                    }, 10)
                }}
                render={
                    () => {
                        hasVisited11_1 = true;
                        return (
                            <>
                                <StageImage ref={contentRef} src={stageImg2} alt="page11-1" />
                                <StyledAnswer 
                                    answers={["12"]} 
                                    onSuccess={(answer) => { 
                                        game.answer(answer)
                                        game.goto('/page12');
                                    }} 
                                    onFailure={() => { game.goto('/wrong1'); }} 
                                    onHint={onShowHint} 
                                />
                            </>
                        )
                    }
                } 
            >
                <img src={gotoBtnImg} alt="go to page3" />
                <img className="hover" src={gotoBtnImg_hover} alt="go to page3" />
            </StyledPressButton>
        </Wrapper>
    )
}

export default { 
    pathname: '/page11', 
    component: withHintModal(Page11)(hint)
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const StyledPressButton = styled(PressButton)`
    display: block;
    margin: 0 auto;

    &::after {
        opacity: 0;
    }
  
    img {
        display: block;
        width: 40vw;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`

const StyledAnswer = styled(Answer)`
    margin-top: 10vw;
`