import { css } from 'styled-components';
import bitroTTF from '../assets/font/bitro.ttf';
import yes24TTF from '../assets/font/YES24.ttf';
import disitalTTF from '../assets/font/disital.ttf';
import ChosunGsTTF from '../assets/font/ChosunGs.ttf';
import NanumTTF from '../assets/font/Nanum.ttf';
import AggroTTF from '../assets/font/Aggro.ttf';
import BaskinTTF from '../assets/font/Baskin.ttf';

export default css`
    @font-face {
        font-family: 'bitro';
        font-style: normal;
        font-weight: normal;
        src: url(${bitroTTF}) format("truetype");
    }
    @font-face {
        font-family: 'yes24';
        font-style: normal;
        font-weight: normal;
        src: url(${yes24TTF}) format("truetype");
    }
    @font-face {
        font-family: 'disital';
        font-style: normal;
        font-weight: normal;
        src: url(${disitalTTF}) format("truetype");
    }
    @font-face {
        font-family: 'gungsuh';
        font-style: normal;
        font-weight: normal;
        src: url(${ChosunGsTTF}) format("truetype");
    }
    @font-face {
        font-family: 'nanum';
        font-style: normal;
        font-weight: normal;
        src: url(${NanumTTF}) format("truetype");
    }
    @font-face {
        font-family: 'aggro';
        font-style: normal;
        font-weight: normal;
        src: url(${AggroTTF}) format("truetype");
    }
    @font-face {
        font-family: 'baskin';
        font-style: normal;
        font-weight: normal;
        src: url(${BaskinTTF}) format("truetype");
    }
`