import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg from './assets/page3.png';
import hint from './assets/hint2.png';

function Page3({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage1');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="page3" />
            <Answer 
                answers={["8237120"]} 
                onSuccess={(answer) => { 
                    game.goto('/page4')
                    game.answer(answer)
                 }} 
                onFailure={() => { game.goto('/wrong1'); }} 
                onHint={onShowHint} 
            />
        </Wrapper>
    )
}

export default { 
    pathname: '/page3', 
    component: withHintModal(Page3)(hint) 
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`
