import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import PressButton from '../../components/PressButton';
import withHintModal, { HintModalRenderProps } from '../../components/withHintModal';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg1 from './assets/page17.png';
import stageImg2 from './assets/page17-1.png';
import hint from './assets/hint9.png';
import gotoBtnImg from './assets/page17-btn.png';
import gotoBtnImg_hover from './assets/page17-btn-hover.png';
import { useRef } from 'react';

let hasVisited9_1 = false;

function Page17({ onShowHint }: HintModalRenderProps) {
    const game = useGame('stage1');
    const { answer } = game.state.stage1.answers.find((answer) => answer.pathname === '/page5-1') as Answer || {}
    const contentRef = useRef<HTMLImageElement>(null);

    return (
        <Wrapper>
            <StageImage src={stageImg1} alt="page17" />
            <Name>
                {answer || ''}
            </Name>
            <StyledPressButton 
                timing={0}
                showContent={hasVisited9_1}
                showButton={true}
                onEnd={() => {
                    window.setTimeout(() => {
                        const contentEl = contentRef.current as HTMLImageElement;
                        
                        window.scrollTo({ top: contentEl.offsetTop, left:0, behavior: 'smooth' })
                    }, 10)
                }}
                render={
                    () => {
                        hasVisited9_1 = true;
                        return (
                            <>
                                <StageImage ref={contentRef} src={stageImg2} alt="page17-1" />
                                <StyledAnswer 
                                    answers={["대한민국 소방관 최고", "대한민국소방관최고", "대한민국소방관 최고","대한민국소방관 최고"]} 
                                    onSuccess={(answer) => { 
                                        game.answer(answer)
                                        game.goto('/page18');
                                    }} 
                                    onFailure={() => { game.goto('/wrong8'); }} 
                                    onHint={onShowHint} 
                                />
                            </>
                        )
                    }
                } 
            >
                <img src={gotoBtnImg} alt="go to page3" />
                <img className="hover" src={gotoBtnImg_hover} alt="go to page3" />
            </StyledPressButton>
        </Wrapper>
    )
}

export default { 
    pathname: '/page17', 
    component: withHintModal(Page17)(hint)
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding-bottom: 20vw;
    min-height: 100vh;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const Name = styled.span`
    position: absolute;
    top: 30%;
    left: 35.7%;
    color: #362b28;
    font-size: 143%;
    background-color: transparent;
    font-family: aggro;
    font-weight: bold;
`

const StyledPressButton = styled(PressButton)`
    display: block;
    margin: 0 auto;

    &::after {
        opacity: 0;
    }
  
    img {
        display: block;
        width: 40vw;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`

const StyledAnswer = styled(Answer)`
    margin-top: 10vw;
`