import React from 'react'
import styled from 'styled-components';
import Answer from '../../components/Answer';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import stageImg from './assets/page5-1.png';
import backgroundImg from './assets/1stage-bg.png';

function Page5_1() {
    const game = useGame('stage1');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="page5-1" />
            <Answer 
                anyAnswer
                placeholder="최대 4글자로 입력해 주세요."
                answers={[]} 
                onSuccess={(answer) => { 
                    game.answer(answer)
                    game.goto('/page6');
                }} 
                onFailure={() => { }}
            />
        </Wrapper>
    )
}

export default { 
    pathname: '/page5-1', 
    component: Page5_1,
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    min-height: 100vh;
    padding-bottom: 80px;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`
