// 이 페이지에서 사용될 모든 이미지, 스타일, 컴포넌트 등
import React from 'react'
import styled from 'styled-components';
import Layout from '../../components/Layout';
import useGame from '../../hooks/useGame';
import backgroundImg from './assets/1stage-bg.png';
import stageImg from './assets/page1.png';
import gotoBtnImg1 from './assets/page1-btn.png';
import gotoBtnImg1_hover from './assets/page1-btn-hover.png';

// 페이지 이동 경로 재설정 | useGame 안 'stage1'은 유지 | 수정하고 싶으면 containers > GameStorage 파일 수정 필요.
// game.gogo('여긴 다음 페이지로')
function Page1() {
    const game = useGame('stage1');

    return (
        <Wrapper>
            <StageImage src={stageImg} alt="page1" />
            <Button onClick={() => { game.goto('/page2') }}>
                <img src={gotoBtnImg1} alt="go to page2" />
                <img className="hover" src={gotoBtnImg1_hover} alt="go to page2" />
            </Button>
        </Wrapper>
    )
}



// 정답을 입력해야하는 페이지가 아닐 경우 : component는 현재 페이지 위치
export default { 
    pathname: '/page1', 
    component: Page1
} as RouteMap;

const Wrapper = styled(Layout)`
    background: url(${backgroundImg}) repeat;
    background-attachment: fixed;
    min-height: 100vh;
    padding-bottom: 20vw;
`

const StageImage = styled.img`
    width: 100%;
    height: auto;
`

const Button = styled.button`
    display: block;
    margin: 0 auto;

    img {
        display: block;
        width: 45vw;
        height: auto;
    }

    img.hover {
        display: none;
    }

    &:hover {
        img:not(.hover) {
            display: none;
        }
        img.hover {
            display: block;
        }
    }
`